:root {
  --column-num: 2;
}

body {
  margin: 0;
}

.people {
  grid-template-columns: repeat(var(--column-num), 220px);
  justify-content: center;
  gap: 40px 10px;
  margin: 34px;
  display: grid;
}

.person {
  background-color: #fff;
  width: 220px;
  height: 231px;
  font-family: Arial, Helvetica, sans-serif;
}

.person__photo {
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.person__img {
  border-radius: 50%;
  width: 200px;
  margin-bottom: 8px;
}

.person__name {
  color: #2f4f4f;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  display: flex;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 84px;
  right: 20px;
}

.person__extra {
  opacity: 0;
  justify-content: center;
  align-items: center;
  transition: opacity 1s, visibility 1s;
  display: flex;
}

.person__extra:hover {
  opacity: 1;
}

@media (width >= 1000px) {
  .people {
    --column-num: 4;
  }
}
